import React, {lazy} from 'react';
import { SepHighlight } from "./DomHelper";
// import { Link } from "react-router-dom";
import Images from "./Images";
import lozad from 'lozad'
//
const BgOverlay = lazy(() => import('./defaults/BgOverlay'))
//

/* BotDiv */
export class BotDiv extends React.Component {

    observer = lozad('.lozad', {
        load: function (el) {
            el.style.backgroundImage = "url('" + el.getAttribute('data-background-image') + "')";
        }
    });

    state = {
        isNonBanner: false,
        isMounted: false,
        isCssLoaded: false
    }

    componentDidMount(){
        this.observer.observe();

        if (this.state.isMounted === false) {
            this.setState({
                isMounted: true
            })
        }
        if(this.props.isNonBanner !== undefined){
            this.setState({
                isNonBanner: this.props.isNonBanner
            })
        }
        else{
            this.setState({
                isNonBanner: false
            })
        }   
    }

    render() {

        if (this.state.isMounted && this.state.isCssLoaded === false) {
            import('./style/footer.css').then(() => {
                this.setState({
                    isCssLoaded: true
                });
            });
        }

        return (
            <div id="botDiv" className="lozad" data-background-image={Images.footBanner}>
                { this.state.isNonBanner ? null : <FooterBanner />} 
                <Footer />
            </div>

        )
    }
}

class FooterBanner extends React.Component{

    render(){
        return(
            <>
                <div className="findMe">
                    <h2 data-aos="fade-down" data-aos-duration="750" data-aos-delay="250">Have a project in mind?</h2>
                    <span className="msgBody" data-aos="fade-down" data-aos-duration="750" data-aos-delay="500">
                        {/* <Link className="btn" to={{ pathname: "/get-in-touch" }}>Get in touch</Link> */}
                        <a className="btn" href="mailto:jeremysohwk92@gmail.com">Drop me an email</a>
                    </span>
                    <BgOverlay />
                </div>
            </>
        )
    }
}

class Footer extends React.Component{


    render(){
        return(
            <div className="footerDiv">
                <div className="foot-item reactSol tablet-hide">
                    made with <img src={Images.reactLogo} alt="reactJs" height="16px" style={{ verticalAlign: "-3px" }} /> React
                        </div>
                <div className="foot-item copyright">
                    Copyright {SepHighlight('&copy;')} {(new Date().getFullYear()) + " "}
                    {/* <a className="alt" href="https://jellydevelops.com/" target="_blank">jellydevelops</a> {SepHighlight('/')}*/} {SepHighlight('jelly')}miso
                        </div>
                <div className="foot-item designBy">
                    <span className="foot-item">designed {SepHighlight('&')} developed by</span> <img src={Images.logoNoAlias} alt="jellymiso" height="31px" />
                </div>
            </div>
        )
    }
}