export class BaseLoader extends React.Component {

    state = {
        isMounted: false,
        isCssLoaded: false
    }

    componentDidMount() {
        if (this.state.isMounted === false) {
            this.setState({
                isMounted: true
            })
        }
    }

    render() {

        if (this.state.isMounted && this.state.isCssLoaded === false) {
            import('./style/loader.css').then(() => {
                this.setState({
                    isCssLoaded: true
                });
            });
        }

        let { className, style, ...props } = this.props;

        return (
            <FullPageOverlay 
                className={
                    "baseApp " 
                    + ((this.state.isMounted && this.state.isCssLoaded) ? "" : "waitInit ")
                    + ((this.props.className) ? className : "")}
                style={{ ...style}}
                {...props}
            >
                <div className="loader">
                    <div className="aquarium">
                        <Jellyfish />
                        <div className="water" />
                    </div>
                    <div className="loading-text">
                        {WavyText("Please\u00a0wait\u00a0while\u00a0the\u00a0jellyfish\u00a0gets\u00a0everything\u00a0ready...", 100, 10, 4000)}
                    </div>
                </div>
            </FullPageOverlay>
           
        )
    }

}

/* Site Notifications */
export class SiteNotice extends React.Component {

    state = {
        status: null,
        message: "",
        timeoutMS: 5000
    }

    componentDidUpdate() {
        let el = this;
        if(this.state.status !== null){
            setTimeout(function () {

                el.setState({
                    status: null,
                    message:""
                })
             
            }, this.state.timeoutMS)
        }


    }

    hideNote(){

    }

    updateNoticeData(statusRet, messageRet, timeoutInMS = 5000) {
        this.setState({
            status: statusRet,
            message: messageRet,
            timeoutMS: timeoutInMS
        })
    }

    render() {
        return (
            <div className={
                "note" + (
                    (this.state.status)
                        ? (
                            (this.state.status.toLowerCase() === "success")
                                ? " success"
                                : " error"
                        )
                        : ""
                )
            }>
                <span>{this.state.message}</span>
            </div>
        )
    }

}


