//=== third party script init ===
import('react-ga').then((ReactGA) => {
    ReactGA.initialize('UA-178663147-1');
    ReactGA.pageview(window.location.pathname + window.location.search);
});
Aos.init();

let s = Skrollr.init({
    forceHeight: false,
    mobileCheck: function () {
        //hack - forces mobile version to be off
        return false;
    }
});
//


class App extends React.Component {

    state = {
        currentBreakpoint: window.innerWidth
    }

    componentDidUpdate(prevProps, prevState) {
        s.refresh(); //reinit skrollr after update
        Aos.refresh();
        //if prev page props and current page props are not the same :: not on the same page
        if(this.props !== prevProps){
        
            let currentPath = this.props.location.pathname.toLowerCase();
            let prevPath = prevProps.location.pathname.toLowerCase();

            //if current page is project pages?
            if (currentPath.indexOf("/projects") >= 0){
                //if it came from another project page
                if (prevPath.indexOf("/projects") >= 0) {
                    setTimeout(function () {
                        window.scrollTo(0, 0);
                    }, 1500);
                }
            }
            //else homepage or contact
            else{
                //if current page is homepage and it came from a project page
                if (currentPath === "/" && prevPath.indexOf("/projects") >= 0){
                    setTimeout(function () {
                        window.scrollTo(0,0);
                    }, 1500);
                }
            }
        }
     

    }
    componentDidMount() {
        s.refresh(); //reinit skrollr after mount
        Aos.refresh();

        window.addEventListener("resize", this.updateBreakPoint);
        // console.log("app mounted")
        // setTimeout(() => {
        //     this.forceUpdate()
        // }, 600)
    }
    componentWillUnmount() {
        window.removeEventListener("resize", this.updateBreakPoint);
    }

    updateBreakPoint = () => {
        this.setState({
            currentBreakpoint: window.innerWidth
        },
            () => {
                s.refresh(); 
            });
    }

    render() {
        return (
            <>
                <Route render={
                    ({ location }) => (
                        <Suspense fallback={<></>}>
                            <TransitionGroup className="transitGrp">
                                <CSSTransition classNames="fade" timeout={{ enter: 1500, exit: 1500 }} key={location.key}>
                                    <TransitionEndTop>
                                        <Switch location={location}>
                                            <Route exact path="/" render={
                                                (props) => <Home s={s} currentBreakpoint={this.state.currentBreakpoint} {...props} />
                                            } />
                                            <Route path="/projects" render={
                                                (props) => <ProjectDetails s={s} currentBreakpoint={this.state.currentBreakpoint} {...props} />
                                            } />
                                            {/* <Route exact path="/get-in-touch" component={Contact} /> */}
                                            <Route path="*" >
                                                <NotFound />
                                            </Route>
                                        </Switch>
                                    </TransitionEndTop>
                                </CSSTransition>
                            </TransitionGroup>
                        </Suspense>
                    )
                } />

                <BaseLoader />
            </>
         
        )
    }
}

export default withRouter(props => <App {...props} />);