import React, {lazy} from 'react';
import { Link} from "react-router-dom";
//
import ReactTooltip from 'react-tooltip';
import PROJECTS from './data/projects';
import TAGS from './data/tags';
import Tags from './Tags';
import { BgOverlay, StringToUrlPath, DecodeHTMLDangerously, NotFound } from "./DomHelper";
import Images from "./Images";
import { BotDiv} from './Footer'
import Config from './data/config';
import lozad from 'lozad'
//

const Slider = lazy(() => import("react-slick"))
const TextFitV2 = lazy(async () => {
        await new Promise(resolve => setTimeout(resolve, 0));
        return import('./defaults/TextFitV2');
    });

//project details pages content
export class ProjectDetails extends React.Component{

    state = {
        isMounted: false,
        isCssLoaded: false
    }

    componentDidMount() {
        this.props.s.refresh();
        // console.log("mounted 1")
        // this.forceUpdate(() => {
        //     console.log("updated 1")
        // });
        

        // setTimeout(() => {
        //     console.log(this.TextFitV2.current)
        //     this.TextFitV2.current.setState({
        //         key: 0
        //     })
        //     this.TextFitV2.current.forceUpdate(() => {
        //         console.log("updated v2")
        //     });
        // }, 100);

        if (this.state.isMounted === false) {
            this.setState({
                isMounted: true
            })
        }

        let sortedArray = this.getContentDataInArray(PROJECTS)
        let currentIndex = this.getCurrentIndexFromData(PROJECTS);
        if (currentIndex !== -1) {
            document.title = "Past Project: " + sortedArray[currentIndex].title + " | " + Config.app.documentTitle;
            // var meta = document.createElement('meta');
            // meta.setAttribute("property", "og:title");
            // meta.content = sortedArray[currentIndex].title;
            // document.getElementsByTagName('head')[0].appendChild(meta);
        }
        
    }

    getCurrentIndexFromData(data){ 
        let currentPath = this.props.location.pathname.toLowerCase();
        currentPath = (currentPath.substr(-1) !== '/' ? currentPath + "/" : currentPath);

        let sortedArray = this.getContentDataInArray(data);

        let currentIndex = sortedArray.findIndex(
            a => ("/projects/" + StringToUrlPath(a.title)).toLowerCase() + "/" === currentPath
        );

        return currentIndex;
    }

    getContentDataInArray(data){
        let sortedArray = data.sort((a, b) => a.title.localeCompare(b.title));
        return sortedArray
    }
    
    render(){

        // 

        let sortedArray = this.getContentDataInArray(PROJECTS)
        let currentIndex = this.getCurrentIndexFromData(PROJECTS);

        if (currentIndex === -1) {
            return (
                <NotFound />
            )
        }
        else{

            if (this.state.isMounted && this.state.isCssLoaded === false) {
                Promise.all([
                    import('./style/project-details.css'),
                    import('./utility/slick/slick.css'),
                    import('./utility/react-tool-tip/tooltip.css'),
                ]).then(([projectDetailCSS, slickCss, utilExtCss]) => {
                    this.setState({
                        isCssLoaded: true
                    });
                });
            }

            let prevProjContent = currentIndex > 0 ? sortedArray[currentIndex - 1] : sortedArray[sortedArray.length - 1]
            let nextProjContent = currentIndex < sortedArray.length - 1 ? sortedArray[currentIndex + 1] : sortedArray[0];
            let { title, description, desc_extend, link, link_text, link_external, coverImg, coverImgMobilePos, image, slogan } = sortedArray[currentIndex];


            let slick_settings = {
                dots: false,
                arrows: true,
                infinite: true,
                speed: 500,
                slidesToShow: 1,
                slidesToScroll: 1,
                responsive: null,
                useCss: true,
                useTransform: true
            };

            return (
                <div className="projectDetail">
                    <div className="misc">
                        <Link className="prev project"
                            to={{pathname: "/projects/" + StringToUrlPath(prevProjContent.title) + "/"}}
                            data-tip={"PREVIOUS PROJECT: " + (prevProjContent != null && prevProjContent.title)}>
                            <img src={Images.prevArrow} alt="previous project" /> Prev<span className="tablet-hide"> Project</span>
                        </Link>
                        <Link className="closeBtn" to={{ pathname: "/" }}>&times; Close<span className="tablet-hide"> Project</span></Link>
                        <Link className="next project"
                            to={{pathname: "/projects/" + StringToUrlPath(nextProjContent.title) + "/"}}
                            data-tip={"NEXT PROJECT: " + (nextProjContent != null && nextProjContent.title)}>
                            Next <span className="tablet-hide">Project </span><img src={Images.nextArrow} alt="next project" />
                        </Link>
                        <ReactTooltip place="bottom" type="light" effect="float" className="tip" />
                    </div>
                    <div className="detail" >
                        <div className="bgWrap">
                            <div className="bgContent">
                                <p className="slogan" data-aos="fade-down" data-aos-duration="750" data-aos-delay="500">{slogan} </p>
                                <TextFitV2 className="titleWrap"
                                    id="titleWrap" settings={{
                                        max: 120,
                                        min: 90,
                                        style: { height: "165px" },
                                        onReady: (min) => {
                                            document.getElementById("titleWrap").style.height = "auto";
                                        },
                                        responsive: [{
                                            breakpoint: 1440,
                                            max: 90,
                                            min: 68,
                                            style: {height: "150px"}
                                        }, {
                                            breakpoint: 960,
                                            max: 90,
                                            min: 55,
                                            style: { height: "135px" }
                                        }, {
                                            breakpoint: 769,
                                            max: 45,
                                            min: 35,
                                            style: { height: "75px" }
                                        }, {
                                            breakpoint: 512,
                                            max: 45,
                                            min: 24,
                                            style: { height: "75px" }
                                        }]
                                    }}
                                    >
                                        <h1 className="title" data-aos="zoom-out" data-aos-duration="750" data-aos-delay="750">
                                            <span className="titleInner">{title}</span>
                                        </h1>
                                    </TextFitV2>
                        
                                <p className="mainDesc" data-aos="fade-right" data-aos-duration="750" data-aos-delay="1000">{description}</p>

                                {link !== null &&
                                    (
                                        <a className="btn hvLight" href={link} rel="noopener noreferrer"
                                            data-aos="fade-up" data-aos-duration="750" data-aos-delay="1250" data-aos-offset="0"
                                            target={link_external === false ? null : "_blank"} >
                                            {link_text != null ? link_text : "Visit Website"}
                                            {
                                                link_external === false ? null : (
                                                    <span className="icon">
                                                        <img src={Images.linkout} alt="Visit Website" />
                                                        <img src={Images.linkoutHover} className="hover" alt="Visit Website" />
                                                    </span>
                                                )
                                            }

                                        </a>
                                    )
                                }
                            </div>
                            <div className="bgDiv">
                                <div className="imgDiv" data-0="top:0px;" data-800="top:250px" 
                                    style={{ 
                                        backgroundImage: "url(" + coverImg + ")",
                                        backgroundPosition: (
                                            (this.props.currentBreakpoint <= 769 && coverImgMobilePos)
                                            ? coverImgMobilePos
                                            : null
                                        )
                                    }}
                                ></div> 
                                {/* <img src={coverImg} alt={title} data-0="top:0px;" data-800="top:250px" /> */}
                                <BgOverlay />
                            </div>
                            <div className="scrollDiv" data-0="opacity:1;" data-400="opacity:0;">
                                <span className="scrollText" >Scroll to explore</span>
                                <span className="scrollDown">
                                    <img src={Images.scrollDown} alt="scroll down" />
                                </span>
                            </div>
                        </div>
                        <div className="showcase">
                            {
                                (typeof desc_extend === 'object'
                                    && (desc_extend.about_text !== undefined || desc_extend.about_title !== undefined))
                                    ? (
                                        <div className="descLong">
                                            <div className="title" data-aos="fade-down" data-aos-duration="750" data-aos-delay="250">
                                                <h2>{desc_extend.about_title === null ? "About the Project" : desc_extend.about_title}</h2>
                                            </div>
                                            <p data-aos="fade-down" data-aos-duration="750" data-aos-delay="500">{desc_extend.about_text}</p>
                                        </div>
                                    )
                                    : null
                            }

                            {
                                (image.gallery1 !== undefined)
                                    ? (
                                        <div className="gallery">
                                            {
                                                (image.gallery1.length > 1)
                                                ? (
                                                    <div className="tablet-show side-arrows">
                                                        <div className="arrow left">
                                                            <img src={Images.scrollLeft} alt="scroll sideways" />
                                                        </div>
                                                        <div className="scroll-text">Scroll Sideways</div>
                                                        <div className="arrow right">
                                                            <img src={Images.scrollRight} alt="scroll sideways" />
                                                        </div>
                                                    </div>
                                                )
                                                : null
                                            }
                                     
                                            {
                                                <div className="firstShowcase" data-aos="fade-down" data-aos-duration="750" data-aos-delay="250">
                                                    <Slider {...slick_settings}>
                                                        {

                                                            image.gallery1.map((img, i) => {
                                                                return (
                                                                    <div key={i} className="gallery-item">
                                                                        <img src={img} alt={title} />
                                                                    </div>
                                                                )
                                                            })
                                                        }
                                                    </Slider>

                                                </div>
                                            }
                                        </div>
                                    )
                                    : null
                            }
                            {
                                (typeof desc_extend === 'object' && desc_extend !== null)
                                ? (
                                    <div className="descExt">
                                        <div className="descRole">
                                        {
                                            <>
                                                <div className="roleHead">
                                                    <div className="title">
                                                        <h2 data-aos="fade-down" data-aos-duration="750" data-aos-delay="250">Project / Role</h2>
                                                        <p className="note" data-aos="fade" data-aos-duration="750" data-aos-delay="250">My role in the project and its scope</p>

                                                        {/* <p>{desc_extend.proj_roles}</p> */}
                                                    </div>
                                                </div>
                                                <div className="roleBase">
                                                    {
                                                        desc_extend.proj_role === null ? "Work In Progress"
                                                            : (
                                                                <>

                                                                    {
                                                                        desc_extend.proj_role.main_role === null ? null
                                                                            : (
                                                                                <div className="roleMain">
                                                                                    <p>
                                                                                        <span data-aos="fade-right" data-aos-duration="750" data-aos-delay="500">role :&nbsp;</span>
                                                                                        <span className="feat" data-aos="fade-left" data-aos-duration="750" data-aos-delay="500">
                                                                                            {desc_extend.proj_role.main_role}
                                                                                        </span>
                                                                                    </p>
                                                                                </div>
                                                                            )
                                                                    }
                                                                    {
                                                                        desc_extend.proj_role.status === null ? null
                                                                            : (
                                                                                <div className="roleEmployment">
                                                                                    <p>
                                                                                        <span data-aos="fade-right" data-aos-duration="750" data-aos-delay="600">status :&nbsp;</span>
                                                                                        <span className="feat" data-aos="fade-left" data-aos-duration="750" data-aos-delay="600">
                                                                                            {desc_extend.proj_role.status}
                                                                                        </span>
                                                                                    </p>
                                                                                </div>
                                                                            )
                                                                    }

                                                                    {
                                                                        desc_extend.proj_role.purpose === null ? null
                                                                            : (
                                                                                <div className="rolePurpose">
                                                                                    <p>
                                                                                        <span data-aos="fade-right" data-aos-duration="750" data-aos-delay="700">scope :&nbsp;</span>
                                                                                        <span className="feat" data-aos="fade-left" data-aos-duration="750" data-aos-delay="700">
                                                                                            {desc_extend.proj_role.purpose}
                                                                                        </span>
                                                                                    </p>
                                                                                </div>
                                                                            )
                                                                    }

                                                                    {
                                                                        desc_extend.proj_role.purpose === null ? null
                                                                            : (
                                                                                <div className="roleDuration">
                                                                                    <p>
                                                                                        <span data-aos="fade-right" data-aos-duration="750" data-aos-delay="800">duration :&nbsp;</span>
                                                                                        <span className="feat" data-aos="fade-left" data-aos-duration="750" data-aos-delay="800">
                                                                                            {desc_extend.proj_role.duration}
                                                                                        </span>
                                                                                    </p>
                                                                                </div>
                                                                            )
                                                                    }
                                                                </>
                                                            )
                                                    }
                                                </div>
                                            </>
                                        }
                                        </div>
                                        <div className="descStack">
                                            <div className="stackWrap">
                                                <div className="stackHead">
                                                    <div className="title">
                                                        <h2 data-aos="fade-down" data-aos-duration="750" data-aos-delay="250">Technical Stack</h2>
                                                    </div>
                                                    <p className="note" data-aos="fade" data-aos-duration="750" data-aos-delay="250">My technical involvement during the project</p>
                                                </div>
                                                <div className="stackBase">
                                                    {
                                                        desc_extend.tech_stack === null ? "Work In Progress"
                                                            : (
                                                                <>
                                                                    {
                                                                        Object.keys(desc_extend.tech_stack).map((techGrp, i) => {
                                                                            if (techGrp === undefined){ return null; }
                                                                            else{
                                                                                let techClass = "";
                                                                                switch (techGrp.toLowerCase()){
                                                                                    case "frontend":
                                                                                        techClass = "stackFront";
                                                                                        break;
                                                                                    case "backend":
                                                                                        techClass = "stackBack";
                                                                                        break;
                                                                                    case "data":
                                                                                        techClass = "stackInfra";
                                                                                        break;
                                                                                    case "infra":
                                                                                        techClass = "stackData";
                                                                                        break;
                                                                                    case "tools":
                                                                                        techClass = "stackTools";
                                                                                        break;
                                                                                    default:
                                                                                        break;
                                                                                }
                                                                                return(
                                                                                    <div className={techClass} key={techGrp}>
                                                                                        <h3 data-aos="fade-right" data-aos-duration="750" data-aos-delay="250">{
                                                                                        (techGrp.toLowerCase() !== "tools") 
                                                                                            ? techGrp.charAt(0).toUpperCase() + techGrp.slice(1)
                                                                                            : "Others"
                                                                                        }</h3>
                                                                                        <p data-aos="fade-right" data-aos-duration="750" data-aos-delay="500">
                                                                                            {
                                                                                                desc_extend.tech_stack[techGrp].map((tech, i) => {
                                                                                                    if (i === desc_extend.tech_stack[techGrp].length - 1) {
                                                                                                        return tech
                                                                                                    }
                                                                                                    else {
                                                                                                        return tech + ", "
                                                                                                    }
                                                                                                })
                                                                                            }
                                                                                        </p>
                                                                                    </div>
                                                                                )
                                                                            }
                                                                        })
                                                                    }


                                                                </>
                                                            )
                                                    }
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                                : null
                            }
                           
                            {
                                (image.gallery2 !== undefined)
                                    ? (
                                        <div className={"gallery2" + ((image.gallery2.isMobileScreen) ? " mobile" : "")}>
                                            {
                                                (image.gallery2.isMobileScreen)
                                                    ? (
                                                        <div className="mobile-show side-arrows">
                                                            <div className="arrow left">
                                                                <img src={Images.scrollLeft} alt="scroll sideways" />
                                                            </div>
                                                            <div className="scroll-text">Scroll Sideways</div>
                                                            <div className="arrow right">
                                                                <img src={Images.scrollRight} alt="scroll sideways" />
                                                            </div>
                                                        </div>
                                                    )
                                                    : null
                                            }
                                            {
                                                (image.gallery2.isMobileScreen)
                                                ? (
                                                    (this.props.currentBreakpoint <= 769)
                                                    ? (
                                                        <Slider {...slick_settings}>
                                                            {
                                                                image.gallery2.data.map((img, i) => {
                                                                    return (
                                                                        <div key={i} className="gallery-item"
                                                                            data-aos={(i + 1) % 2 === 0 ? "fade-up" : "fade-down"} data-aos-duration="750"
                                                                            data-aos-delay={((i + 1) * 250) + 250}>
                                                                            <img src={img} alt={title} />
                                                                        </div>
                                                                    )
                                                                })
                                                            }
                                                        </Slider>
                                                    )
                                                    : (
                                                        image.gallery2.data.map((img, i) => {
                                                            return (
                                                                <div key={i} className="gallery-item"
                                                                    data-aos={(i + 1) % 2 === 0 ? "fade-up" : "fade-down"} data-aos-duration="750"
                                                                    data-aos-delay={((i + 1) * 250) + 250}>
                                                                    <img src={img} alt={title} />
                                                                </div>
                                                            )
                                                        })
                                                    )
                                                )
                                                : (
                                                    image.gallery2.map((img, i) => {
                                                        return (
                                                            <div key={i} className="gallery-item"
                                                                data-aos={i === 0 ? "fade-down" : "fade-up"} 
                                                                data-aos-duration="750"
                                                                data-aos-delay={i === 0 ? "500" : "250"}>
                                                                <img src={img} alt={title} />
                                                            </div>
                                                        )
                                                    })
                                                )
                                     
                                            }
                                        </div>
                                    )
                                    : ""
                            }

                            {
                                (desc_extend.takeaway !== undefined)
                                    ? (
                                        <div className="keyTakeaway">
                                            <div className="keyTakeaway-inner">
                                                <h2 data-aos="fade-down" data-aos-duration="750" data-aos-delay="250">Key Takeaway</h2>
                                                {DecodeHTMLDangerously(desc_extend.takeaway, {
                                                    'data-aos': "fade-down",
                                                    'data-aos-duration': "750",
                                                    'data-aos-delay': "500"
                                                })}
                                            </div>
                                        </div>
                                    )
                                    : ""

                            }
                        </div>
                    </div>

                    <BotDiv />
                </div>

            )
        }

  
    }

}

//home page projects section
export class Projects extends React.Component {

    state = {
        activeItem: -1
    }


    tagClick = (tagID) => {
        this.setState({
            activeItem: tagID
        }, () => {
            // console.log("tagID", tagID)
        })
    }

    render() {
        let projectArray = [], arrayOfProjects = [];
        return (
            <div className="projects">
                {
                    (() => {
                        for (var i = 0; i < PROJECTS.length; i++) {
                            if (this.state.activeItem === -1) {
                                projectArray.push(PROJECTS[i]);
                            }
                            else {
                                if (PROJECTS[i].tagIDs.indexOf(this.state.activeItem) >= 0) {
                                    projectArray.push(PROJECTS[i]);
                                }
                            }
                        }

                        let sortedArray = projectArray.sort((a, b) => a.title.localeCompare(b.title));
                        for (var k = 0; k < sortedArray.length; k++) {
                            arrayOfProjects.push(
                                <Project onClick={this.props.projectOnClick} key={sortedArray[k].id} project={sortedArray[k]}/>
                            )
                        }

                    })()
                }
                <h2>Past Works</h2>
                <div className="tagList">
                    <Tags tagOnClick={this.tagClick} activeTagItem={this.state.activeItem} />
                </div>
                <div className="desc">Showcasing {arrayOfProjects.length} selected projects over a total of more than 50 delivered projects:</div>

                <div className="projectWrap">
                    {arrayOfProjects}
                </div>
            </div>
        )
    }
}

//home page individual project
class Project extends React.Component{
 
    observer = lozad('.lozad', {
        load: function (el) {
            el.src = el.getAttribute('data-src');
            }
    });

    componentDidMount() {
        this.observer.observe();
    }

    render(){

        const {title, coverImgThumb, description, tagIDs} = this.props.project;
        let path = StringToUrlPath(title);
        
        return(
            <div className="project-item" >
                <img className="lozad" data-src={coverImgThumb} alt={title} />
                <BgOverlay />
                <div className="item-content">
                    <div className="contentWrap">
                        <h3>{title}</h3>
                        <p>{description}</p>

                    </div>
                    <div className="miscWrap">
                        <span className="tags">
                            {
                                (() => {
                                    let tagArray = [], arrayOfTags = [];
                                    for (var i = 0; i < TAGS.length; i++) {
                                        if (typeof tagIDs == 'object' && tagIDs !== null) {
                                            for (var j = 0; j < tagIDs.length; j++) {
                                                if (TAGS[i].id === tagIDs[j]) {
                                                    tagArray.push(TAGS[i]);
                                                }
                                            }
                                        }
                                    }

                                    let sortedArray = tagArray.sort((a, b) => a.name.localeCompare(b.name));
                                    for (var k = 0; k < sortedArray.length; k++) {
                                        arrayOfTags.push(<span key={sortedArray[k].id}>{sortedArray[k].name}</span>)
                                    }
                                    return arrayOfTags;
                                })()

                            }
                        </span>
                        <Link className="btn" to={{ pathname: "/projects/" + path + "/"}}>Learn More</Link>
                        {/* <span className="btn" onClick={() => this.props.onClick(this.props.project)}>Learn More</span> */}
                    </div>
                </div>
            </div>
           
        )
    }
}
